<template>
    <section class="create-memorial">
        <div class="create-memorial__container">

            <header class="create-memorial__header">
                <router-link :to="{ name: 'memorials.create.welcome' }" tag="button"
                             class="default-arrow-btn create-memorial__btn-header">
                    <img class="arrow-right" src="~@/assets/img/icon/arrow-right.svg">
                    {{ $ml.get('create.memorial.step1.header.button') }}
                </router-link>

                <div class="create-memorial__header-wrapper">
                    <router-link :to="{ name: 'home' }">
                        <img class="create-memorial__img" src="~@/assets/img/icon/welcome-screen-logo.svg" alt="Logo">
                    </router-link>
                </div>
            </header>
            <div class="create-memorial__form__wrapper_desciption">
                <h1 class="create-memorial__title third-header">
                    {{ $ml.get('create.memorial.step1.title') }}
                </h1>
                <div class="create-memorial__decor"></div>
                <p class="create-memorial__title-text second-text">
                    {{ $ml.get('create.memorial.step1.desc') }}
                </p>
            </div>

            <div class="create-memorial__form">
                <div class="create-memorial__form__wrapper">

                    <div class="create-memorial__form__wrapper__inputs">
                        <form class="form">
                            <h5 class="form__title form__mb">
                                {{ $ml.get('create.memorial.step1.form.title') }}
                              <img class="post-plan__options-img" src="~@/assets/img/icon/ask-yelow.svg" alt="" @click="isTooltipShow = !isTooltipShow">
                            </h5>
                            <div class="form__group form__mb" :class="{'invalid': bindClassInvalid('name')}">
                                <img class="form__icon hover" src="~@/assets/img/icon/close-input.svg"
                                     v-if="showDeleteIcon('name')"
                                     @click.prevent="model.name=null"
                                >
                                <img class="form__icon" src="~@/assets/img/icon/error-red.svg"
                                     v-else-if="showAlertIcon('name')"
                                >
                                <input v-model="model.name" type="text" class="inputText form__input" required
                                       :class="{'focus': bindClassFocus('name')}"
                                       @focus="focusFields.name=true"
                                       @blur="focusFields.name=false"
                                >
                                <transition name="fade">
                                    <label class="form__floating-label"
                                           v-show="showFloatingLabel('name')">
                                        {{ $ml.get('create.memorial.step1.form.name') }}
                                    </label>
                                </transition>
                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('name') }}</span>
                                </div>
                            </div>

                            <div class="form__group form__mb" :class="{'invalid': bindClassInvalid('slug')}">
                                <img class="form__icon hover" src="~@/assets/img/icon/close-input.svg"
                                     v-if="showDeleteIcon('slug')"
                                     @click.prevent="model.slug=null"
                                >
                                <img class="form__icon" src="~@/assets/img/icon/error-red.svg"
                                     v-else-if="showAlertIcon('slug')"
                                >
                                <input v-model="model.slug" type="text" class="inputText form__input" required
                                       :class="{'focus': bindClassFocus('slug')}"
                                       @focus="focusFields.slug=true"
                                       @blur="focusFields.slug=false"
                                >
                                <transition name="fade">
                                    <label class="form__floating-label"
                                           v-show="showFloatingLabel('slug')">
                                        {{ $ml.get('create.memorial.step1.form.slug') }}
                                    </label>
                                </transition>
                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('slug') }}</span>
                                </div>
                            </div>

                            <div class="form__group form__mb" :class="{'invalid': bindClassInvalid('birthDate')}">
                                <img slot="afterDateInput" class="form__icon hover"
                                     src="~@/assets/img/icon/close-input.svg"
                                     v-if="showDeleteIcon('birthDate')"
                                     @click.prevent="birthDate=null;model.birthDate=null"
                                >
                                <img slot="afterDateInput" class="form__icon" src="~@/assets/img/icon/calendar-24px.svg"
                                     v-else-if="!focusFields.birthDate && !birthDate"
                                >

                                <input v-model="birthDate" type="text" class="inputText form__input" required
                                       v-on:keydown.capture.prevent.stop
                                       :class="{'focus': bindClassFocus('birthDate')}"
                                       @focus="focusFields.birthDate=true"
                                       @blur="closeBirthdayPicker"
                                       @click.prevent="openBirthdayPicker"
                                >
                                <transition name="fade">
                                    <label class="form__floating-label"
                                           v-show="showFloatingLabel('birthDate')">
                                        {{ $ml.get('create.memorial.step1.form.birth') }}
                                    </label>
                                </transition>

                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('birthDate') }}</span>
                                </div>

                                <datepicker
                                        ref="birthday"
                                        :language="dtLocale"
                                        :required="true"
                                        :input-class="{'inputText form__input': true, 'focus': bindPickerClassFocus('birthDate')}"
                                        id="birthday"
                                        class="datepicker"
                                        @selected="birthdayPickerSelected"
                                >
                                </datepicker>
                            </div>

                            <div class="form__group form__mb" :class="{'invalid': bindClassInvalid('deathDate')}">
                                <img slot="afterDateInput" class="form__icon hover"
                                     src="~@/assets/img/icon/close-input.svg"
                                     v-if="showDeleteIcon('deathDate')"
                                     @click.prevent="deathDate=null;model.deathDate=null"
                                >
                                <img slot="afterDateInput" class="form__icon" src="~@/assets/img/icon/calendar-24px.svg"
                                     v-else-if="!focusFields.deathDate && !deathDate"
                                >

                                <input v-model="deathDate" type="text" class="inputText form__input" required
                                       v-on:keydown.capture.prevent.stop
                                       :class="{'focus': bindClassFocus('deathDate')}"
                                       @focus="focusFields.deathDate=true"
                                       @blur="closeDeathPicker"
                                       @click.prevent="openDeathPicker"
                                >
                                <transition name="fade">
                                    <label class="form__floating-label"
                                           v-show="showFloatingLabel('deathDate')">
                                        {{ $ml.get('create.memorial.step1.form.death') }}
                                    </label>
                                </transition>

                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('deathDate') }}</span>
                                </div>

                                <datepicker
                                        ref="death"
                                        :language="dtLocale"
                                        :required="true"
                                        :input-class="{'inputText form__input': true, 'focus': bindPickerClassFocus('deathDate')}"
                                        id="death"
                                        class="datepicker"
                                        @selected="deathPickerSelected"
                                >
                                </datepicker>
                            </div>

                            <!-- load-photo -->

                            <div class="load-photo form__mb" v-if="avatarPreview">
                                <img class="load-photo__close" src="~@/assets/img/icon/close-white.svg" alt=""
                                     @click.prevent="avatarPreview=null">
                                <div class="load-photo__wrapper">

                                    <div class="load-photo__text-box">
                                        <div class="load-photo__title">
                                            {{ $ml.get('create.memorial.step1.form.photo.title') }}
                                        </div>
                                        <div class="load-photo__text">
                                            {{ $ml.get('create.memorial.step1.form.photo.desc') }}
                                        </div>
                                    </div>

                                    <div class="load-photo__img-box">
                                        <img :src="avatarPreview" alt="">
                                    </div>
                                </div>
                            </div>

                            <div class="form__photo-block form__mb" v-show="!avatarPreview">
                                <input @change="uploadAvatar" accept="image/*" id="avatar-file" ref="avatar"
                                       class="form__file" type="file">
                                <div class="form__photo-block-inner">
                                    <div>
                                        <p class="form__photo-block-title">
                                            {{ $ml.get('create.memorial.step1.form.photo.title') }}
                                        </p>
                                        <p class="form__photo-block-text">
                                            {{ $ml.get('create.memorial.step1.form.photo.desc') }}
                                        </p>
                                    </div>
                                    <img onclick="document.getElementById('avatar-file').click()"
                                         class="form__photo-plus" src="~@/assets/img/icon/form-plus.svg" alt="">
                                    <img src="~@/assets/img/icon/form-human.svg" alt="">
                                </div>
                            </div>

                            <div v-if="!isUpdated" class="form__group form__mb"
                                 :class="{'invalid': bindClassInvalid('relationToMemorialPerson')}">
                                <select type="text" class="form__select"
                                        required
                                        v-model="model.relationToMemorialPerson"
                                        :class="{'not-selected': !model.relationToMemorialPerson}">
                                    <option selected value="0">{{ $ml.get('create.memorial.step1.form.relation') }}
                                    </option>
                                    <option
                                            v-for="(relation, rk) in $ml.get('create.memorial.step1.form.relations')"
                                            :key="`relation-${rk}`"
                                            :value="rk">{{ relation }}
                                    </option>
                                </select>
                                <div class="form__box-img">
                                    <img src="~@/assets/img/icon/arrow-down-blue.svg" alt="">
                                </div>
                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('relationToMemorialPerson') }}</span>
                                </div>
                            </div>

                            <div class="form__group form__mb" :class="{'invalid': bindClassInvalid('relation')}"
                                 v-show="model.relationToMemorialPerson == 17 || isUpdated"
                            >
                                <img class="form__icon hover" src="~@/assets/img/icon/close-input.svg"
                                     v-if="!isUpdated && showDeleteIcon('relation')"
                                     @click.prevent="model.relation=null"
                                >
                                <img class="form__icon" src="~@/assets/img/icon/error-red.svg"
                                     v-else-if="showAlertIcon('relation')"
                                >
                                <input v-model="model.relation" type="text" class="inputText form__input" required
                                       :class="{'focus': bindClassFocus('relation')}"
                                       @focus="focusFields.relation=true"
                                       @blur="focusFields.relation=false"
                                       v-on:keydown="handleKeyDown"
                                >
                                <transition name="fade">
                                    <label class="form__floating-label"
                                           v-show="showFloatingLabel('relation')">
                                        {{ $ml.get('create.memorial.step1.form.relation') }}
                                    </label>
                                </transition>
                                <div class="form__error-box">
                                    <img class="form__error-img" src="~@/assets/img/icon/error-red.svg" alt="">
                                    <span class="form__error">{{ showErrorText('relation') }}</span>
                                </div>
                            </div>

                            <button @click.prevent="submitForm" type="button" class="form__button default-second-btn">
                                <img class="arrow-left" src="~@/assets/img/icon/arrow-left.svg">
                                {{ $ml.get('create.memorial.step1.form.button') }}
                            </button>
                        </form>
                    </div>

                </div>
            </div>
        </div>

        <sidebar :step="1"/>
        <HelpCenter/>

      <Tooltip
          v-if="isTooltipShow"
          :label="$ml.get('create.memorial.help.label')"
          :withTalkLink="false"
          @close="isTooltipShow = false"
      >
        <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('create.memorial.help.text') }}
        </span>
        </div>
      </Tooltip>
    </section>
</template>

<script>
    // @ is an alias to /src
    import Vue from 'vue';
    import Datepicker from 'vuejs-datepicker';
    import {he, en} from 'vuejs-datepicker/dist/locale'
    import {mapGetters} from "vuex";
    import sidebar from '@/views/memorials/create/partials/sidebar';
    import SimpleVueValidation from 'simple-vue-validator';
    import HelpCenter from '@/components/HelpCenter.vue';
    import Tooltip from "@/components/Tooltip";


    const Validator = SimpleVueValidation.Validator;

    Vue.use(SimpleVueValidation);

    export default {
        name: 'MemorialCreateStep1',
        components: {
            Datepicker,
            sidebar,
            HelpCenter,
            Tooltip,
        },
        data() {
            return {
                isTooltipShow: false,
                dtLocale: he,
                birthDateModPh: false,
                selectedAvatar: false,
                avatarPreview: null,
                birthDate: null,
                deathDate: null,
                model: {
                    name: null,
                    slug: null,
                    birthDate: null,
                    deathDate: null,
                    profilePhotoId: null,
                    relationToMemorialPerson: 0,
                    relation: null,
                },
                focusFields: {
                    name: false,
                    slug: false,
                    birthDate: false,
                    deathDate: false,
                    relation: false,
                    relationToMemorialPerson: false
                },
            }
        },
        validators: {
            'model.name': function (value) {
                return Validator.value(value).required().minLength(1).maxLength(254).regex('^(\\w+?|\\d+?|\\s+?|[`\'\"()-_=+*!?\u0590-\u05FF]+?)+$');
            },
            'model.slug': function (value) {
                return Validator.value(value).required().minLength(1).maxLength(254).regex('^[a-z0-9-]+$');
            },
            'model.deathDate': function (value) {
                return Validator.value(value).required();
            },
            'model.relationToMemorialPerson': function (value) {
                return Validator.value(value).required().regex('[1-9]+');
            },
            'model.relation': function (value) {
                return Validator.value(value).required().minLength(1).maxLength(254).regex('^(\\w+?|\\d+?|\\s+?|[`\'\"()-_=+*!?\u0590-\u05FF]+?)+$');
            },
            'model.birthDate': function (value) {
                return Validator.value(value).required();
            },
        },
        computed: {
            ...mapGetters({
                errors: 'memorial/getErrors',
                formIsLoading: 'memorial/formIsLoading',
                uploadedFile: 'memorial/getFile',
                memorial: 'memorial/getMemorialFormData',
                memorialCreated: 'memorial/getMemorialForCreating',
                step: 'memorial/getStep',
                isUpdated: 'memorial/getIsUpdated',
            })
        },
        methods: {
            handleKeyDown(e) {
                if (this.isUpdated) {
                    e.preventDefault();
                    return false;
                }

                return true;
            },
            bindClassInvalid(key) {
                return this.errors[key] || this.validation.hasError(`model.${key}`);
            },
            bindClassFocus(key) {
                return this.focusFields[key] || this.errors[key] || this.validation.hasError(`model.${key}`);
            },
            bindPickerClassFocus(key) {
                return this.errors[key] || !this.focusFields[key] || this.validation.hasError(`model.${key}`);
            },
            showDeleteIcon(key) {
                return this.model[key] && !this.errors[key] && !this.validation.hasError(`model.${key}`);
            },
            showAlertIcon(key) {
                return this.errors[key] || this.validation.hasError(`model.${key}`);
            },
            showErrorText(key) {
                return this.errors[key] || this.validation.firstError(`model.${key}`);
            },
            showFloatingLabel(key) {
                return this.errors[key] || this.focusFields[key] || !this.model[key] || this.validation.hasError(`model.${key}`);
            },
            reset: function () {
                this.model = {
                    name: null,
                    slug: null,
                    birthDate: null,
                    deathDate: null,
                    profilePhotoId: null,
                    relationToMemorialPerson: 0,
                    relation: null,
                };
                this.validation.reset();
            },
            closeBirthdayPicker() {
                this.focusFields.birthDate = false;
                this.$refs.birthday.close();
            },
            openBirthdayPicker() {
                this.$refs.birthday.showCalendar();
            },
            birthdayPickerSelected(value) {
                this.model.birthDate = this.$dateUtils.formatDate(value, 'yyyy-MM-dd',  '-', this.$ml.current);
                this.birthDate = this.$dateUtils.formatDate(value, 'dd MMM yyyy',  '-', this.$ml.current);
            },
            closeDeathPicker() {
                this.focusFields.deathDate = false;
                this.$refs.death.close();
            },
            openDeathPicker() {
                this.$refs.death.showCalendar();
            },
            deathPickerSelected(value) {
                this.model.deathDate = this.$dateUtils.formatDate(value, 'yyyy-MM-dd', '-', this.$ml.current);
                this.deathDate = this.$dateUtils.formatDate(value, 'dd MMM yyyy', '-', this.$ml.current);
            },
            submitForm() {
                let $this = this;
                let relations = this.$ml.get('create.memorial.step1.form.relations');

                if (!$this.isUpdated && $this.model.relationToMemorialPerson != '17' && relations[$this.model.relationToMemorialPerson] !== undefined) {
                    $this.model.relation = relations[$this.model.relationToMemorialPerson];
                }

                this.$validate()
                    .then(async function (success) {
                        // console.log(success);

                        if (success) {
                            let file = $this.uploadedFile;

                            if ($this.$refs.avatar.files.length) {
                                let avatar = Array.from($this.$refs.avatar.files)[0];
                                const fileData = {
                                  file: avatar,
                                  type: 'image',
                                };
                                await $this.$store.dispatch('memorial/upload', fileData);
                                file = $this.uploadedFile
                            }

                            if ($this.isUpdated) {
                                if (!$this.avatarPreview) {
                                    file = null;
                                    $this.$store.commit('memorial/setUploadedFile', null);
                                }

                                await $this.$store.dispatch('memorial/update', {
                                    id: $this.memorialCreated.id,
                                    model: $this.model,
                                    file
                                });
                            } else {
                                await $this.$store.dispatch('memorial/create', {model: $this.model, file});
                            }

                            if ($this.step == 2) {
                                await $this.$router.push({name: 'memorials.create.step2'});
                            }
                        }
                    });
            },
            uploadAvatar() {
                this.selectedAvatar = true;
                let file = Array.from(this.$refs.avatar.files)[0];
                this.avatarPreview = URL.createObjectURL(file);
            },
        },
        mounted() {
            this.$store.commit('memorial/setErrors');
            this.$store.commit('memorial/setFormIsLoading', false);
            this.$store.commit('memorial/setStep', 1);

            if (this.isUpdated && this.memorialCreated != null && !this.memorialCreated.paid) {
                this.model = Object.assign(this.model, this.memorial);
                this.model.editVersion = this.memorialCreated.editVersion;

                this.deathDate = this.$dateUtils.formatDate(new Date(this.model.deathDate), 'dd MMM yyyy', '-', this.$ml.current);
                this.birthDate = this.$dateUtils.formatDate(new Date(this.model.birthDate), 'dd MMM yyyy', '-', this.$ml.current);

                if (this.uploadedFile) {
                    this.avatarPreview = this.uploadedFile.url;
                }
            } else {
                this.$store.commit('memorial/setDefaultData');
            }
        },
        async created() {
            // console.log(this.memorialCreated);
            this.dtLocale = this.$ml.current == 'en' ? en : he;

            if (this.memorialCreated != null) {
                await this.$store.dispatch('memorial/getMemorialForCreating');
            }

            if (this.$ml.current == 'en') {
                SimpleVueValidation.extendTemplates({
                    check: 'This field must be check.',
                    error: 'Error.',
                    required: 'This field is required.',
                    float: 'Must be a number.',
                    integer: 'Must be an integer.',
                    number: 'Must be a number.',
                    lessThan: 'Must be less than {0}.',
                    lessThanOrEqualTo: 'Must be less than or equal to {0}.',
                    greaterThan: 'Must be greater than {0}.',
                    greaterThanOrEqualTo: 'Must greater than or equal to {0}.',
                    between: 'Must be between {0} and {1}.',
                    size: 'Size must be {0}.',
                    length: 'Length must be {0}.',
                    minLength: 'Must have at least {0} characters.',
                    maxLength: 'Must have up to {0} characters.',
                    lengthBetween: 'Length must between {0} and {1}.',
                    in: 'Must be {0}.',
                    notIn: 'Must not be {0}.',
                    match: 'Not matched.',
                    regex: 'Invalid format.',
                    digit: 'Must be a digit.',
                    email: 'Invalid email.',
                    url: 'Invalid url.',
                    optionCombiner: function (options) {
                        if (options.length > 2) {
                            options = [options.slice(0, options.length - 1).join(', '), options[options.length - 1]];
                        }
                        return options.join(' or ');
                    }
                });
            } else {
                SimpleVueValidation.extendTemplates({
                    check: '    .',
                    error: 'Error.',
                    required: '  .',
                    float: 'Must be a number.',
                    integer: 'Must be an integer.',
                    number: 'Must be a number.',
                    lessThan: '    {0}.',
                    lessThanOrEqualTo: '     - {0}.',
                    greaterThan: 'Must be greater than {0}.',
                    greaterThanOrEqualTo: 'Must greater than or equal to {0}.',
                    between: 'Must be between {0} and {1}.',
                    size: 'Size must be {0}.',
                    length: '   {0}.',
                    minLength: '  {0} .',
                    maxLength: '   {0} .',
                    lengthBetween: '   {0}  {1}.',
                    in: 'Must be {0}.',
                    notIn: 'Must not be {0}.',
                    match: 'Not matched.',
                    regex: '  .',
                    digit: 'Must be a digit.',
                    email: ' .',
                    url: 'Invalid url.',
                    optionCombiner: function (options) {
                        if (options.length > 2) {
                            options = [options.slice(0, options.length - 1).join(', '), options[options.length - 1]];
                        }
                        return options.join(' or ');
                    }
                });
            }
        },
    }
</script>

<style lang="scss">
    .vdp-datepicker {
        position: absolute !important;
    }

    .datepicker {
        input {
            position: absolute !important;
            opacity: 0;
        }
    }

    .vdp-datepicker__calendar {
      width: 300px !important;
    }
</style>

<style lang="scss">
    @import "~@/assets/scss/pages/create-memorial.scss";
</style>